
.testimonials__icon{
    text-align: center;
    margin-bottom: 2.25em;
    
    .testimonials__quote-icon{
        fill: $primary-color;
        height: 2em;
        width: 2em;
    }
}

.testimonials__align{
    display: flex;
    justify-content: center;
}

.testimonials__quote{
    font-family: $font-light;
    font-weight: 300;
    font-size: 1.2em;
    color: rgb(140,148,148);
    text-align: center;
    line-height: 1.3em;
    margin-bottom: 5em;
    max-width: 27em;
}

.testimonials__img{
    height: 4em;
    width: 4em;
    padding: 0.2em;
    border: 2px solid $primary-color;
    border-radius: 200%;
    float: left;
    margin-right: 0.5em;
}

.testimonials__description{
    float: left;
    padding-top: 1em;
}

.description__name,
.description__profession{
    font-family: $font-regular;
    font-weight: normal;
}

.description__profession{
    text-align: left;
    font-size: 0.9em;
    color: $secundary-color;
}

.description__name{
    font-size: 1.2em;
    color: $primary-color;
}

.testimonials__users{
    text-align: center;
    margin-bottom: 5em;
    padding-left: 1.5em;
}

@media only screen and (max-width: 1023px){
    
    .testimonials__user,
    .testimonials__img,
    .testimonials__description{
        float: none;
    }
    .description__profession{ text-align: center;}
    .testimonials__user{ margin-bottom: 2em;}
    
    .testimonials__users{
        margin-bottom: 0;
        :last-of-type{
            margin-bottom: 0;
        }
    }
    
}

